import UniLoadMore from '@/components/UniLoadMore/UniLoadMore.vue'
export default {
	components:{
		UniLoadMore
	},
	data(){
		return {
			list:[],
			page:1,
			noData:'more'
		}
	},
	onShow(){
		this.page = 1;
		this.reqListArr();
		this.page = 2;
		this.reqListArr();
	},
	onReachBottom(){
		if(this.noData === 'noMore'){
			return false;
		}else{
			this.page++;
			this.reqListArr();
		}
	},
	methods:{
		reqListArr(){
			this.noData = 'loading'
			this.$request.POST({
				url:this.$api.apiUrl.POST_MY_KELIST,
				data:{
					page:this.page
				}
			}).then(res=>{
				let result = res.list;				
				if(result.length > 4){
					this.list = this.list.concat(result);
					this.noData='more';
				}else{
					this.noData = 'noMore';
				}
			})
		}
	}
}
